import {eId, eClass, darkLightMode} from '../../components/helpers/StylingHelpers';

function EventListStyle(){
    darkLightMode("events");
    darkLightMode("form-control");

    setStyle();

    var userAgent = navigator.userAgent || navigator.vendor || window.opera;
    if (!(/android/i.test(userAgent))) {
        window.addEventListener('resize', setStyle);
    }
}

export default EventListStyle;

function setStyle(){
    if(!eId('events')){
        return false;
    }
    
    eId('events').style.height = eId('nav').offsetTop + 'px';
    eId('events').style.padding = eId('events').offsetWidth * 0.02 + 'px';

    eClass('eventTable')[0].style.height = (eId('events').offsetHeight - eClass('eventTable')[0].offsetTop) * 0.98 + 'px';
    eClass('table')[0].style.height = (eClass('addEvent')[0].offsetTop - eClass('table')[0].offsetTop) * 0.96 + 'px';
    eClass('tbody')[0].style.height = eClass('table')[0].offsetHeight - (eClass('tbody')[0].offsetTop - eClass('table')[0].offsetTop) + 'px';

    if(window.innerWidth >= 1024){
        eClass('input-group')[0].style.borderRadius = eClass('input-group')[0].offsetWidth * 0.012 + 'px';
    } else if(window.innerWidth >= 768){
        eClass('input-group')[0].style.borderRadius = eClass('input-group')[0].offsetWidth * 0.015 + 'px';
    }

    //eClass('table-sm')[0].style.fontSize = eId('app').offsetWidth * 0.03 + 'px';
    //console.log(eClass('table')[0].className);
    if(window.innerWidth <= 576){
        eId('events').className = eId('events').className.replace('pt-3', 'pt-1');
        eClass('title')[0].className = eClass('title')[0].className.replace('pb-3', 'pb-2');
        eClass('search')[0].className = eClass('search')[0].className.replace('mb-3', 'mb-2');
        eClass('eventTable')[0].className = eClass('eventTable')[0].className.replace(' table ', ' table-sm ');
    } else {
        eId('events').className = eId('events').className.replace('pt-1', 'pt-3');
        eClass('title')[0].className = eClass('title')[0].className.replace('pb-2', 'pb-3');
        eClass('search')[0].className = eClass('search')[0].className.replace('mb-2', 'mb-3');
        eClass('eventTable')[0].className = eClass('eventTable')[0].className.replace(' table-sm ', ' table ');
    }

    eClass('addEvent')[0].style.width = eClass('addEvent')[0].offsetHeight + 'px';
    eClass('addEventBtn')[0].style.width = eClass('addEventBtn')[0].offsetHeight + 'px';
    //console.log(eClass('Nav')[0].offsetTop + eId('nav').offsetHeight);
}
